
@import url("./assets/normalize.css");
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  min-width: 1024px;
}
a {
  text-decoration: none;
  color: #2c3e50;
}
#app {
  width: 100%;
  min-width: 1024px;
  min-height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 1px;
  position: relative;
}
#app .app-bg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -100;
    opacity: 0.3;
    background: url("../static/imgs/bg2.jpg") no-repeat;
    background-size: cover;
}
#app .loader-page {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    background: #fff;
}
#app .loader-page .loader-progress {
      width: 300px;
      height: 300px;
      margin: auto;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
}
#app .loader-page .loader-progress .van-progress {
        margin-top: 50px;
}
#app .loader-page .loader-progress .van-progress .van-progress__pivot {
          width: 35px;
}



