
.onepiano-app-wrap {
  width: 100%;
  min-height: 100%;
  position: relative;
}
.onepiano-app-wrap .trade-mark {
    width: 300px;
    height: 60px;
    line-height: 60px;
    padding-left: 50px;
    margin-left: 5%;
    background: url("../../static/imgs/music_piano.png") no-repeat;
    background-size: 35px 35px;
    background-position: 0 center;
}
.onepiano-app-wrap .trade-mark .trade-mark-txt {
      font-size: 20px;
      font-weight: 500;
      color: #1296db;
}
.onepiano-app-wrap .trade-mark .trade-mark-txt .trade-mark-txt--en {
        font-style: italic;
}
.onepiano-app-wrap .random-lyric {
    width: 100%;
    padding: 20px 0 20px;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
}
.onepiano-app-wrap #audioEffectCanvas {
    display: none;
}
.onepiano-app-wrap .piano-wrap {
    width: 90%;
    margin: 20px auto;
}
.onepiano-app-wrap .piano-wrap .piano-band {
      width: 100%;
      height: 35px;
      background: url("../../static/imgs/band2.png") no-repeat #000;
      background-size: 110px 30px;
      background-position: center center;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap {
      width: 100%;
      background: #333;
      letter-spacing: -10px;
      overflow: hidden;
      position: relative;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .piano-key:hover {
        cursor: pointer;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .auto-key-active {
        background: #f5deb3 !important;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .wkey {
        display: inline-block;
        letter-spacing: normal;
        width: 2.775%;
        height: 100%;
        margin: 0 auto;
        background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, white), color-stop(92%, #fbfbfb), color-stop(93%, gainsboro), color-stop(97%, white));
        background: linear-gradient(white 10%, #fbfbfb 92%, gainsboro 93%, white 97%);
        border: solid 1px #333;
        border-radius: 0 0 5px 5px;
        position: relative;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .wkey:active {
          background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #eee), color-stop(60%, #ddd), color-stop(93%, #bbb), color-stop(97%, #ccc));
          background: linear-gradient(#eee 10%, #ddd 60%, #bbb 93%, #ccc 97%);
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .wkey .keytip {
          width: 100%;
          color: #333;
          text-align: center;
          position: absolute;
          font-size: 14px;
          bottom: 5%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .wkey .keytip .keyname {
            margin-bottom: 5px;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .wkey .keytip .notename {
            color: #1296db;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .wkey-active {
        background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #eee), color-stop(60%, #ddd), color-stop(93%, #bbb), color-stop(97%, #ccc));
        background: linear-gradient(#eee 10%, #ddd 60%, #bbb 93%, #ccc 97%);
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-wrap {
        width: 20%;
        height: 0;
        position: absolute;
        top: 0;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-wrap1 {
        left: 0;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-wrap2 {
        left: 19.5%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-wrap3 {
        left: 39%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-wrap4 {
        left: 58.3%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-wrap5 {
        left: 77.7%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey {
        display: inline-block;
        width: 10%;
        height: 70%;
        background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #000), color-stop(85%, #565656), color-stop(90%, #000));
        background: linear-gradient(#000 10%, #565656 85%, #000 90%);
        border-radius: 0 0 3px 3px;
        position: absolute;
        top: 0;
        overflow: hidden;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey:active {
          background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #565656), color-stop(90%, #000), to(#222));
          background: linear-gradient(#565656 10%, #000 90%, #222 100%);
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey .keytip {
          width: 100%;
          color: white;
          position: absolute;
          left: 0;
          bottom: 5%;
          font-size: 14px;
          overflow: hidden;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey .keytip .keyname {
            width: 100%;
            margin-left: -25%;
            text-align: center;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey-active {
        background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, #565656), color-stop(90%, #000), to(#222));
        background: linear-gradient(#565656 10%, #000 90%, #222 100%);
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey:nth-child(1) {
        left: 9%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey:nth-child(2) {
        left: 23%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey:nth-child(3) {
        left: 50%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey:nth-child(4) {
        left: 65%;
}
.onepiano-app-wrap .piano-wrap .piano-key-wrap .bkey:nth-child(5) {
        left: 79%;
}
.onepiano-app-wrap .piano-options {
    width: 90%;
    margin: 20px auto;
    padding: 5px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    position: relative;
}
.onepiano-app-wrap .piano-options .option-item {
      height: 33px;
      line-height: 33px;
      margin: 0 10px;
}
.onepiano-app-wrap .piano-options .option-item .van-switch {
        cursor: pointer;
}
.onepiano-app-wrap .piano-options .option-item .van-switch--on {
        background: #1296db;
}
.onepiano-app-wrap .piano-options .option-item span {
        display: inline-block;
        vertical-align: top;
        margin: 0 10px;
}
.onepiano-app-wrap .piano-options .music-list-btn {
      position: absolute;
      height: 35px;
      line-height: 35px;
      right: 20px;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      cursor: pointer;
      color: #333;
      font-weight: bold;
}
.onepiano-app-wrap .piano-options .music-list-btn img {
        width: 32px;
        height: 32px;
        display: inline-block;
        vertical-align: top;
}
.onepiano-app-wrap .piano-options .music-list-btn span {
        display: inline-block;
        vertical-align: top;
        margin-left: 7px;
}
.onepiano-app-wrap .music-list {
    width: 30%;
    height: 100%;
    padding: 10px;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(30%, rgba(200, 200, 200, 0.9)), color-stop(50%, rgba(220, 220, 220, 0.8)), to(rgba(230, 230, 230, 0.8)));
    background: linear-gradient(rgba(200, 200, 200, 0.9) 30%, rgba(220, 220, 220, 0.8) 50%, rgba(230, 230, 230, 0.8) 100%);
    z-index: 200;
}
.onepiano-app-wrap .music-list .music-list__title {
      margin: 20px 0;
      color: #333;
}
.onepiano-app-wrap .music-list .music-list__title .van-icon {
        padding-right: 10px;
}
.onepiano-app-wrap .music-list .music-list__title .music-list__close {
        float: right;
        color: #333;
        cursor: pointer;
}
.onepiano-app-wrap .music-list .music-list__title .music-list__close:hover {
          color: #ef496f;
}
.onepiano-app-wrap .music-list .music-list__item {
      cursor: pointer;
}
.onepiano-app-wrap .music-list .music-list__item:hover {
        color: #1296db;
}
.onepiano-app-wrap .music-list-mask.user-involve {
    width: 70%;
    height: 100%;
    background: rgba(10, 10, 10, 0.9);
    border-right: solid 1px #fff;
    z-index: 100;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap {
      width: 90%;
      height: 100%;
      margin: 0 auto;
      padding: 20px 0;
      color: #eee;
      overflow: scroll;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap .content {
        text-indent: 32px;
        line-height: 25px;
        margin: 10px 0;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap .picture {
        width: 80%;
        margin: 0 auto;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap .textarea {
        width: 100%;
        padding: 7px;
        margin: 10px 0;
        background: #ddd;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap .play-btn {
        width: 100px;
        height: 35px;
        line-height: 35px;
        text-align: center;
        background: #1296db;
        color: white;
        border: none;
        cursor: pointer;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap .play-btn:hover {
          opacity: .85;
}
.onepiano-app-wrap .music-list-mask.user-involve .user-involve-wrap .tip-txt {
        color: #ef496f;
        margin: 10px 0;
}
.onepiano-app-wrap span.code {
    padding: 0 5px;
    margin: 0 5px;
    color: #ef496f;
    background: #eee;
    text-align: center;
}



